he TV news network identity, the specific show on the network, and the text snippet of the transcribed audio. This dataset provides the ability to compare the TV networks over time on the subject of climate change in order to answer the research question we have previously posed. We followed standard Natural Language Processing (NLP) text preprocessing by removing punctuation and numbers, converting all letters to lower-case (the data was already provided as lowercase), lemmatizing, removing standard English stopwords & corpus-specific stopwords, and tokenizing the data into words. We conducted our analysis with two distinct analysis streams: Frequency Analysis and Content Analysis. Our Frequency Analysis entailed methods of time-series analysis of climate change mentions by the different TV networks over time and dynamic time-warping & STL decomposition. Content Analysis used methods of TF-IDF document embeddings, Cosine Similarity as a proxy for content similarity between documents, and Topic Modeling & Change-Point Detection. The above figure was created as part of an exploratory component of the TF-IDF Embedding analysis, in which we wished to extract the most important words to each of the networks across the entire corpus. ")])])])]),_c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('img',{staticClass:"rounded img-fluid pb-3",attrs:{"src":require("../../../public/assets/networks_and_years_cosine_similarity.png"),"alt":"Second slide"}}),_c('div',{staticClass:"row justify-content-center pb-4"},[_c('div',{staticClass:"col-12 col-md-9"},[_c('h5',[_vm._v("Content Analysis: TF-IDF Document Embedding & Cosine Similarity")]),_c('h6',[_vm._v("Methodology")]),_c('p',[_vm._v(" My contribution to this project was primarly focused in the Content Analysis stream, specifically TF-IDF document embeddings & Cosine Similarity between documents. To conduct our content analysis, we needed to featurize the news snippets, or collections of snippets, which form the documents in our corpus. I first formed a document for each TV network in our dataset (e.g. all snippets for CNN), and transformed the documents into a L2-normalized unit vector TF-IDF vector embedding. From this featurization, I formed a document-term matrix, where the rows correspond to the TF-IDF embedding of a document and the column represents a unique word in the corpus (~34k words). Thus, entry i, j corresponds to the normalized TF-IDF score of word j in document i (i.e. word j's relative importance for the ith document). I also constructed document-term matrices for documents representing each year of our dataset (e.g. all snippets in 2009) as well as for documents constructed from networks in specific years (e.g. all CNN snippets in 2015). Finally, for each of the document-term matrices mentioned above, I calculated the pairwise cosine similarity between the document embeddings to yield a measure of content similarity between the documents. A heatmap constructed from the computed cosine similarities between the network in each year documents is shown above. ")]),_c('h6',[_vm._v("Main Findings")]),_c('p',[_vm._v(" The main findings from my analysis were that the "),_c('strong',[_vm._v("content of climate mentions in the latter years of the dataset, 2016-2020, are most dissimilar to earlier years in the dataset, 2009-2012. We note that these years correspond roughly to Obama's (D) first term as president (2008-2012) and Donald Trump's (R) presidency (2016-2020).")]),_vm._v(" Additionally, in the years 2010 & 2018, the content of MSNBC differed greatly from the other news networks in those years and with other networks and itself in other years. This similarly occurred for CNN in 2012 & 2013. Lastly, with the exception of these years, "),_c('strong',[_vm._v("the similarity of content of climate mentions between CNN and MSNBC, the liberal-leaning networks, has been increasing over the years, although the pairwise content similarity between all of the networks is fairly high over time.")])])])])]),_c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('img',{staticClass:"rounded img-fluid pb-3",attrs:{"src":require("../../../public/assets/final-project-overview.png"),"alt":"Second slide"}}),_c('div',{staticClass:"row justify-content-center pb-4"},[_c('div',{staticClass:"col-12 col-md-9"},[_c('h5',[_vm._v("Results")]),_c('p',[_vm._v(" Climate change TV news media coverage frequency and content appears to be significantly driven by political events more so than environmental factors. The frequency of climate change mentions follow similar patterns by network, with clear influence of political events such as the 2009 UNCCC, 2015 Paris Agreement, and 2019 Democratic primary debates driving climate news coverage. This is reflected in the content of the climate mentions over time as words describing the political events occurring at the time tend to be the most important words for all of the networks in that specific year. This is further seen by the tendency of different networks in the same year to have high content similarity. Topic analysis also finds that the majority of the 15 topics found from the topic analysis had significant changes in mean on some of the topics at the time of Donald Trump's inauguration. ")])])])])])])])])
