},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"carousel-item active cc-carousel-item"},[_c('div',{staticClass:"row align-items-center justify-content-around pb-4"},[_c('div',{staticClass:"col-12"},[_c('img',{staticClass:"rounded img-fluid",attrs:{"id":"overview-pic","src":require("../../../../public/assets/masters-thesis-overview.png"),"alt":"First slide"}})]),_c('div',{staticClass:"col-12 col-md-9 pb-4"},[_c('h5',[_vm._v("Thesis Document & Code and Related Open-source Python Packages")]),_c('p',[_vm._v(" This was a project I worked on as part of my thesis that I wrote as a research assistant at the Urban Risk Lab (URL) at MIT for my Master of Engineering in Electrical Engineering and Computer Science degree. This research investigated the development of machine learning models for assisting crisis managers in gaining situational awareness from crowdsourced crisis data for enhanced crisis response. This presentation briefly introduces the motivation behind this work, explains how the investigation unfolded (i.e. methodology), and discusses the main findings and implications from the research for the broader crisis informatics community. Added bonus at the end featuring an overview of adjacent work done conducted by undergrads that I supervised during my time on this project. "),_c('br'),_c('br'),_vm._v(" The published thesis document can be found "),_c('a',{attrs:{"href":"https://dspace.mit.edu/handle/1721.1/144911","target":"_blank"}},[_vm._v("here.")]),_c('br'),_c('br'),_vm._v(" The code for this thesis was written in Python as packages we developed as well as Jupyter Notebooks. "),_c('a',{attrs:{"href":"https://github.com/dyllew/towards-automated-assessment-of-crowdsourced-crisis-reporting","target":"_blank"}},[_vm._v("Here's the GitHub Repo.")]),_c('br'),_vm._v(" With this thesis, we produced two open-source Python packages to better enable readability, reusability, and reproducibility of the computational utilities derived for performing various experiments and analysis on crowdsourced crisis image and text data: "),_c('ul',[_c('li',[_c('a',{attrs:{"href":"https://pypi.org/project/url-image-module/0.27.0/","target":"_blank"}},[_vm._v("URL Image Module")]),_vm._v(" - Utilities for training, testing, and predicting with pretrained Convolutional Neural Networks for classifying crowdsourced crisis image data and constructing & analyzing annotated datasets")]),_c('li',[_c('a',{attrs:{"href":"https://pypi.org/project/url-text-module/0.6.1/","target":"_blank"}},[_vm._v("URL Text Module")]),_vm._v(" - Utilities for featurizing crisis text data, training and testing with a variety of classification machine learning models, and visualizing clusters of featurized text data")])]),_c('br'),_vm._v(" This research was supported by a grant from Google.org and the Tides Foundation. ")])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('div',{staticClass:"row align-items-center justify-content-around pb-5"},[_c('div',{staticClass:"col-12 col-md-9"},[_c('h5',[_c('strong',[_vm._v("Abstract")])]),_c('p',[_vm._v(" The availability of information during a climate crisis event is critical for crisis managers to assess and respond to crisis impact. During crisis events, affected residents post real-time crisis updates on platforms such as "),_c('a',{attrs:{"href":"https://riskmap.mit.edu/japan.html","target":"_blank"}},[_vm._v("RiskMap")]),_vm._v(" and "),_c('a',{attrs:{"href":"https://twitter.com/?lang=en","target":"_blank"}},[_vm._v("Twitter")]),_vm._v(". These updates provide localized information, which has the potential to enhance crisis awareness and response. However, with limited resources, crisis managers may endure information overload from the inundation of these updates. Prior work has demonstrated the potential of machine learning (ML) methodologies to mitigate this problem. We have identified limitations in the prior work including the lack of involvement of crisis managers in the development and evaluation of a ML methodology. ")]),_c('p',[_vm._v(" To address these limitations, we propose a novel framework and ML methodology which investigate the efficacy of various ML methods in enhancing crisis awareness and response beyond model performance metrics. This framework aims to iteratively embed the information needs and priorities of crisis managers during crisis into the design of the ML methodology. We cooperated with crisis managers in Fukuchiyama City (FC), a city in Japan which is susceptible to flood events, and analyzed crowdsourced crisis image and text data from past FC flood events. We devised the Flood Presence image classification task, constructed Train/Dev/Test splits, and annotated images from FC. We report a weighted F1 score of 92.1% on the test split and 82.5% on the FC images. Using the results of our image analysis ML methodology and the insights we gained from crisis managers, we iterated on the design of our text analysis ML methodology. This led to the creation of the Human Risk text classification task which is tailored to a subset of the identified information needs of the crisis managers. To align with the priorities of crisis managers for this task, we determined the model evaluation metric to be the F2 score. We report an F2 score of 92.8% on an FC crisis text test dataset, which is a significant improvement over the baseline score of 43.4%. ")]),_c('h5',[_c('strong',[_vm._v("Research Question")])]),_c('strong',{attrs:{"id":"research-question"}},[_vm._v(" In collaborating with crisis managers, how can machine learning methods be utilized and evaluated to effectively reduce the information overload of crowdsourced data on crisis managers during flood events for enhanced crisis awareness and response? ")])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('div',{staticClass:"row align-items-center justify-content-around pb-5"},[_c('div',{staticClass:"col-12 col-md-9"},[_c('h5',[_vm._v("Main Research Aims")]),_c('p',{attrs:{"id":"research-aims"}},[_c('ol',[_c('li',[_c('strong',[_vm._v("To reduce information overload during a crisis")]),_vm._v(" using accurate, efficient, automated image and text classification of crisis reports by machine learning (ML) models during crisis.")]),_c('li',[_c('strong',[_vm._v("To embed tacit knowledge, information needs, and decision-making priorities of crisis managers")]),_vm._v(" into the designed ML methodology.")]),_c('li',[_c('strong',[_vm._v("To evaluate methodology in collaboration with crisis managers")]),_vm._v(", e.g. crisis managers in Fukuchiyama, Japan.")]),_c('li',[_c('strong',[_vm._v("To incorporate evaluation results and iterate on the ML methodology")]),_vm._v(" in order to better reach aim of using AI to assist crisis managers during crisis.")])]),_vm._v(" We investigate various ML techniques that can be applied to mitigate information overload for crisis managers during crisis events while also assessing if those techniques can satisfy the information needs and priorities of crisis managers through qualitative and quantitative evaluation. ")]),_c('h5',[_vm._v("Novel Framework")]),_c('p',[_vm._v(" To achieve the aims above we develop a novel framework in the crisis informatics community consisting of the following components: "),_c('ul',[_c('li',[_c('strong',[_c('u',[_vm._v("Classification Task Creation")])]),_vm._v(" - We develop new classification tasks using labels provided to us by crisis managers and labels present in open-source datasets")]),_c('li',[_c('strong',[_c('u',[_vm._v("Data Annotation Procedure")])]),_vm._v(" - We open-souce the annotation guide we develop for greater transparency into the procurement of human-annotated data that is used to train and evaluate ML models")]),_c('li',[_c('strong',[_c('u',[_vm._v("Interannotator Agreement/Data Reliability Analysis")])]),_vm._v(" - After performing an annotation effort on data provided by crisis managers, we analyze the quality of the annotations through interannotator agreement analysis to demonstrate the importance of understanding data quality prior to using it for ML purposes")]),_c('li',[_c('strong',[_c('u',[_vm._v("Model Development and Evaluation; Per-Class Performance Analysis")])]),_vm._v(" - The metrics we used to evaluate models are derived either from metrics reported in the literature or, more notably, "),_c('strong',[_vm._v("metrics determined from insights provided by crisis managers directly")]),_vm._v(". Additionally, we consider issues of class imbalance and report per-class performance and confusion matrices to provide more granular insight into model performance. Finally, we establish baselines to compare against the models we develop to assess the degree to which the models we develop outperform the baseline")]),_c('li',[_c('strong',[_c('u',[_vm._v("Qualitative Analysis through Workshops with Crisis Managers")])]),_vm._v(" - We sought to cooperate with crisis managers with the intent of using this framework to iteratively design ML systems, such as the specific classification tasks performed by the models and their associated performance metric(s), by iteratively incorporating feedback and insights from crisis managers, so that the system better aligns with their information needs and decision-making priorities")])]),_vm._v(" This framework situates "),_c('i',[_vm._v("Model Development and Evaluation")]),_vm._v(", which is commonplace in prior work, as part of a larger, contextualized analysis. On that note, we expand on the notion of "),_c('i',[_vm._v("Model Development and Evaluation")]),_vm._v(" beyond what is typically seen in the prior work. ")])])])])
