},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('div',{staticClass:"row align-items-center justify-content-center pb-4"},[_c('div',{staticClass:"col-12 col-md-9"},[_c('h4',[_vm._v("Discussion & Implications of Study")]),_c('h5',[_vm._v("Involvement of Crisis Managers in the development & iteration of ML methodology")]),_c('p',[_vm._v(" While crisis classification tasks have been published in the literature, to the best of our knowledge, "),_c('strong',[_vm._v("this work is the first of its kind to both involve & incorporate the insights of crisis managers into an ML methodology")]),_vm._v(" to reduce information overload during crisis. This is best seen from the image annotation workshops we held with EOC and the incorporation of those results into our text analysis ML methodology. ")]),_c('h5',[_vm._v("Investigation of Non-English Crisis Text ")]),_c('p',[_vm._v(" While we have developed the infrastructure to develop machine learning text models for any language, "),_c('strong',[_c('u',[_vm._v("we underscore our investigation of Japanese crisis text")]),_vm._v(" as prior work has focused on applying NLP techniques to "),_c('u',[_vm._v("predominantly English crisis text.")])])]),_c('h5',[_vm._v("Contextualized Framework towards better human-centered design of an AI system")]),_c('p',[_c('strong',[_vm._v("Our novel framework contextualizes model performance (e.g. F1, F2, precision, etc.) as part of a broad approach to assessing the efficacy of using ML in reducing information overload.")]),_vm._v(" That is, we consider other important aspects in the design of our system in order to better serve those we aim to assist, crisis managers. We investigate classification task creation, data quality & interannotator agreement analysis, issues of class imbalance, involving crisis managers in the development process, and the determination of a performance metric & corresponding performance baselines ")])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('div',{staticClass:"row align-items-center justify-content-center pb-4"},[_c('div',{staticClass:"col-12 col-md-9"},[_c('h4',[_vm._v("Summary of Main Contribution")]),_c('p',[_vm._v(" With this thesis, we have introduced and exemplified a framework which aims to embed all of the mentioned considerations into the process of designing, developing, and iterating on a ML methodology for enhancing crisis awareness and response. "),_c('strong',[_vm._v("Most notably, this framework situates model development and evaluation, which is commonplace in prior work, as one piece of a broader, contextualized understanding of the efficacy ML methodologies can have for crisis managers in mitigating information overload from crowdsourced crisis reports.")]),_vm._v(" Additionally, this framework "),_c('strong',[_vm._v("promotes the iterative development of AI systems")]),_vm._v(" which is informed "),_c('strong',[_vm._v("from the insights gained from engaging with crisis managers, aiming to address this gap in prior work.")])]),_c('p',[_vm._v(" This framework is only the beginning for similar work in this domain, as the development of AI systems and ML methodologies for mitigating information overload of crisis managers has many complex intricacies for which we only scratch the surface in our attempt to broaden this discussion within the crisis informatics field. "),_c('strong',[_vm._v("We contribute this framework and the full exhibition of the principles and analyses contained within it to work closer towards a goal worth striving for: enhanced crisis awareness and response from automated assessment of crowdsourced crisis reporting.")])]),_c('p',[_vm._v(" In the next slides, we further detail the specifics of the other contributions of this thesis. ")])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('div',{staticClass:"row align-items-center justify-content-center pb-4"},[_c('div',{staticClass:"col-12 col-md-9"},[_c('h4',[_vm._v("Contributions 1/3")]),_c('h5',[_vm._v("Open-source Python Packages/Code")]),_c('p',[_vm._v(" To ensure the reuse of the analysis conducted in this work, we release two open-source Python packages: One for the "),_c('a',{attrs:{"href":"https://pypi.org/project/url-image-module/0.27.0/","target":"_blank"}},[_vm._v("Image Analysis Module")]),_vm._v(" and the other for the "),_c('a',{attrs:{"href":"https://pypi.org/project/url-text-module/0.6.1/","target":"_blank"}},[_vm._v("Text Analysis Module")]),_vm._v(". These packages include utilities for training, testing, and prediction using the models presented in this work, computing statistics for interannotator agreement, and computing metrics for model performance. For text data, there are also utilities for featurization of text, performing nested cross validation, and clustering. We note that there are utilities for plotting such as methods for producing the plots shown throughout this project. ")]),_c('p',[_vm._v(" In addition to these packages, we release an "),_c('a',{attrs:{"href":"https://github.com/dyllew/towards-automated-assessment-of-crowdsourced-crisis-reporting","target":"_blank"}},[_vm._v("open-source repository")]),_vm._v(" containing Jupyter notebooks, relevant documents (e.g. the mentioned annotation guide), and other code required to reproduce the experiments and reuse the analysis conducted in this work. ")]),_c('h5',[_vm._v("Flood Presence Task Creation, Labeled Image Dataset, and Performance Benchmark")]),_c('p',[_vm._v(" Since we focused on flood crises, we defined the image prediction task of Flood Presence classification. The Flood Presence task is the binary classification task of determining whether or not there is presence of flood in an image. We construct a labeled dataset for the task consisting of approx. 23.6𝑘 images by combining various open-source datasets which were labeled with labels useful for this task, although they were originally developed for other adjacent tasks. We contribute this dataset for further research in crisis informatics. In addition, we provide Train/Dev/Test splits and an associated benchmark performance on the test split using a state-of-the-art Convolutional Neural Network (CNN) model, EfficientNet-B1. ")]),_c('h5',[_vm._v("Data Annotation Procedure and Analysis of Interannotator Agreement")]),_c('p',[_vm._v(" We developed a procedure for annotating images in order to label the unlabeled image data provided to us by our partners in Fukuchiyama, Japan. This procedure included creating an annotation guide to assist annotators in their labeling. This guide included the name of each task, the names of the mutually-exclusive classes associated with each task, and an associated description and an example image for each class. We then had annotators from the Urban Risk Lab independently annotate the images using this guide. ")]),_c('p',[_vm._v(" After the annotation effort was completed, we were able analyze the interannotator agreement between the annotators and construct ground-truth labels for these images. We computed interannotator reliability statistics to get a sense of how reproducible the annotation procedure was for each task as well as to have transparency of the data quality prior to using it for ML purposes. Finally, we created ground-truth datasets using these labels for the Fukuchiyama images to use in evaluating the image classification models we developed. ")])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('div',{staticClass:"row align-items-center justify-content-center pb-4"},[_c('div',{staticClass:"col-12 col-md-9"},[_c('h4',[_vm._v("Contributions 2/3")]),_c('h5',[_vm._v("Classification and Clustering of Crowdsourced Japanese Crisis Text")]),_c('p',[_vm._v(" Research in crisis informatics on crowdsourced crisis data focuses mostly on English, and thus research on crowdsourced Japanese crisis text is sparse. The Text Analysis Module developed in this work focused exclusively on Japanese text data. We explored various numerical representations of the Japanese crisis text reports provided by our partners and developed a pipeline for preprocessing the raw text and producing the numerical representations. Additionally, our partners provided labels along with the text reports, so we experimented with classifying the text. Lastly, we explored the text data using unsupervised learning, specifically, we employed clustering methods to help inform development of classification tasks in future work ")]),_c('h6',[_vm._v("Pipeline for Japanese Crisis Text Preprocessing, Tokenization, and Featurization")]),_c('p',[_vm._v(" In order to use the text reports as input to the various ML models employed in this work, we represent the raw text string of each report as a numerical vector, or featurization. Depending on the featurization we choose to use for a text report, we may first preprocess the text. This preprocessing included various steps including tokenization, stopword removal, and lemmatization, which we performed using open-source software (i.e. tokenizer and lemmatizer) and publicly available data (i.e. stopwords list) for the Japanese language. We provide a pipeline for preprocessing and performing the following featurizations on Japanese text: "),_c('ul',[_c('li',[_vm._v("n-gram Bag-of-Words (BOW)")]),_c('li',[_vm._v("n-gram Term Frequency-Inverse Document Frequency (TF-IDF)")]),_c('li',[_vm._v("Pretrained Japanese Masked Language Modeling (MLM) BERT Model with Classification (CLS) Pooling Embedding")])]),_vm._v(" The resultant feature vectors representing the text enabled us to use them as input to ML models. Thus, we can then employ classification and clustering techniques on the text data. ")]),_c('h6',[_vm._v("Human Risk Task Creation and Performance Metric Determination")]),_c('p',[_vm._v(" We devised a new text classification task, Human Risk classification. The human risk text classification task determines whether or not a crisis text report indicates if there are people in need of rescue from a crisis. This includes people being unable to evacuate due to physical disability (such as unable to use stairs), surrounding conditions (such as being trapped in a submerged car), and/or being in need of life-saving emergency medical care. This classification task was unique among the classification tasks presented in this work because it was devised using labels that came with the text reports given to us by our crisis management partners. Relatedly, we determine the metric to use in model performance evaluation using the insights we gained from our partners. ")]),_c('h6',[_vm._v("Exploratory Analysis of Japanese Crisis Text using Unsupervised Learning")]),_c('p',[_vm._v(" With the intention of finding cohesive groupings within the Japanese crisis text corpus, which can inform the development of future text classification tasks, we devise a pipeline for featurizing Japanese crisis text, reducing the high-dimensional text feature vector to 2 dimensions, and clustering the data. We evaluate this pipeline both quantitatively and qualitatively, experimenting with various text featurizations including unigram TF-IDF features and pretrained Japanese MLM BERT with CLS Pooling embeddings mentioned above, t-Distributed Stochastic Neighbor Embedding (t-SNE) and Principle Component Analysis (PCA) for dimensionality reduction, and finally K-means and K-medoids for the algorithm which clusters the data. ")]),_c('p',[_vm._v(" After we determine the optimal combination of text embedding, dimensionality reduction technique, and clustering algorithm, we create brief summaries of each cluster using the unigrams with highest TF-IDF score for each cluster and the closest reports (by euclidean distance) within each cluster to the cluster center to help in the determination of a human-interpretable label for each cluster. Lastly, a member of the Urban Risk Lab at MIT who is fluent in Japanese used these summaries to determine an interpretable label to accompany each cluster found. We thus provide various labels which can be used for classification experiments and analysis in a future work. ")])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('div',{staticClass:"row align-items-center justify-content-center pb-4"},[_c('div',{staticClass:"col-12 col-md-9"},[_c('h4',[_vm._v("Contributions 3/3")]),_c('h6',[_vm._v("Quantitative and Qualitative Evaluation in Japanese Flood Crisis Context")]),_c('p',[_vm._v(" Prior work has typically evaluated ML methods using quantitative measures, mainly classification performance metrics, e.g. accuracy, precision, recall, F1, and AUROC (Area Under the Receiver Operating Characteristic Curve) and their macro and micro variants. However, "),_c('strong',[_vm._v("with the framework we present in this thesis, we aimed to expand the evaluation of the efficacy ML models have in reducing information overload to not only include similar quantitative measures mentioned above, but also qualitative evaluation derived from engaging with our crisis management partners.")]),_vm._v(" Beyond having good performance, we hoped to use the qualitative evaluation used in this study to gain a broader understanding of the efficacy a model can provide crisis managers in mitigating information overload and gaining situational awareness. ")]),_c('p',[_vm._v(" We held image annotation workshops with various crisis managers and aimed to understand what type of information they seek to gain from a crowdsourced image during a flood crisis event. With their insights, we began to understand how our models can be refined or improved, or how new models can be created in order to better serve the information needs of crisis managers more effectively, such as by tailoring the labels and their associated meanings to the information needs of crisis managers suggested from their annotations. Additionally, we gained more insight into the appropriate metrics to use when evaluating models based on the priorities of crisis managers as it relates to the task. From these workshops, we share key lessons that can influence the design of this framework and AI-augmented crisis information systems of the future. In fact, within this work, we used the lessons learned from the image analysis workshops to assist us in determining the performance metric to use when developing models for the human risk text classification task. This exercise exhibited the principle of iterative development our framework intends to promote. ")])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('div',{staticClass:"row align-items-center justify-content-center pb-4"},[_c('div',{staticClass:"col-12 col-md-9"},[_c('h4',[_vm._v("Adjacent Projects")]),_c('img',{staticClass:"rounded img-fluid",attrs:{"id":"overview-pic","src":require("../../../../public/assets/adjacent-projects.png"),"alt":"First slide"}}),_c('p',[_vm._v(" During my time on this project, I defined and mentored undergraduate research projects that were adjacent to my research. These projects included novel topics in the field of Crisis Informatics such as: "),_c('ul',[_c('li',[_c('strong',[_c('u',[_vm._v("Interpretability in Image and Text Classification Models:")])]),_c('p',[_vm._v(" Investigated interpretability algorithms such as GradCAM (Class Activation Mapping) on image classification models used to classify crisis report images and Local Interpretable Model-Agnostic Explaination (LIME) on text classification models used to classify report text in an effort to increase the interpretability of the models which would be employed during crisis and potentially improve model performance through relabeling & retraining. ")])]),_c('li',[_c('strong',[_c('u',[_vm._v("Multilabel Image Classification:")])]),_c('p',[_vm._v(" Investigated CNNs for image classification tasks which can perform multilabel classification (as opposed to single label classification) in the crisis informatics context as most classification tasks in the literature were single-label. ")])]),_c('li',[_c('strong',[_c('u',[_vm._v("Towards Establishing Interannotator Agreement Standards & Tools in the Crisis Informatics Community:")])]),_c('p',[_vm._v(" Investigated the establishment of standards and analysis tools for understanding interannotator agreement (and disagreement) on human-annotated datasets in the crisis informatics community for both single-label and multilabel classification tasks as such standards and tools did not exist in the crisis informatics community. ")])]),_c('li',[_c('strong',[_c('u',[_vm._v("Development of a Crisis Management Dashboard and Simulations using ML Models and their Predictions:")])]),_c('p',[_vm._v(" Investigated the development of an interactive dashboard for crisis managers to use during a crisis event that visualizes the predictions of various machine learning models on a map to provide situational summarization on individual report and aggregate report levels. A simulation was constructed using past citizen crisis reports and predictions on those reports by trained machine learning models. ")])])])])])])])
